<div class="language-yaml ext-yml line-numbers-mode"><pre v-pre class="language-yaml"><code>module.exports = <span class="token punctuation">{</span>

    <span class="token key atrule">themeConfig</span><span class="token punctuation">:</span> <span class="token punctuation">{</span>
        <span class="token key atrule">donate</span><span class="token punctuation">:</span> <span class="token punctuation">{</span>

            //赞赏页面，支付二维码，推荐放置两张图片链接
            <span class="token key atrule">donateImg</span><span class="token punctuation">:</span> <span class="token punctuation">[</span>
                <span class="token string">"https://ooszy.cco.vin/img/blog-public/wxpay.png"</span><span class="token punctuation">,</span>
                <span class="token string">"https://ooszy.cco.vin/img/blog-public/zfbpay.jpg"</span><span class="token punctuation">,</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>

