            //是否在关于页面显示 默认显示
            <span class="token key atrule">aboutPage</span><span class="token punctuation">:</span> <span class="token boolean important">true</span><span class="token punctuation">,</span>

            //显示在赞赏页面的信息
            <span class="token key atrule">donateProduct</span><span class="token punctuation">:</span> <span class="token punctuation">[</span>
                <span class="token punctuation">{</span>
                    //名字
                    <span class="token key atrule">name</span><span class="token punctuation">:</span> <span class="token string">"奶茶"</span><span class="token punctuation">,</span>

                    //图片地址
                    <span class="token key atrule">img</span><span class="token punctuation">:</span> <span class="token string">"https://ooszy.cco.vin/img/blog-public/nc.jpeg"</span><span class="token punctuation">,</span>

                    //价格
                    <span class="token key atrule">price</span><span class="token punctuation">:</span> <span class="token number">18</span><span class="token punctuation">,</span>

                    //前缀
                    <span class="token key atrule">prefix</span><span class="token punctuation">:</span> <span class="token string">"$"</span><span class="token punctuation">,</span>
                <span class="token punctuation">}</span><span class="token punctuation">,</span>
                <span class="token punctuation">{</span>
                    <span class="token key atrule">name</span><span class="token punctuation">:</span> <span class="token string">"全味奶茶"</span><span class="token punctuation">,</span>
                    <span class="token key atrule">img</span><span class="token punctuation">:</span> <span class="token string">"https://ooszy.cco.vin/img/blog-note/image-20210911233612031.png?"</span><span class="token punctuation">,</span>
                    <span class="token key atrule">price</span><span class="token punctuation">:</span> <span class="token number">11</span><span class="token punctuation">,</span>
                    <span class="token key atrule">prefix</span><span class="token punctuation">:</span> <span class="token string">"￥"</span><span class="token punctuation">,</span>
                <span class="token punctuation">}</span><span class="token punctuation">,</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>

            //是否显示在线支付的订单信息，如果需要开启，请自己写支付接口，自己修改源码，默认关闭
            <span class="token key atrule">onlineList</span><span class="token punctuation">:</span> <span class="token boolean important">true</span><span class="token punctuation">,</span>

            //用户赞赏列表数组
            <span class="token key atrule">donateList</span><span class="token punctuation">:</span> <span class="token punctuation">[</span>
                <span class="token punctuation">{</span>
                    //用户名
                    <span class="token key atrule">name</span><span class="token punctuation">:</span> <span class="token string">"qsyyke"</span><span class="token punctuation">,</span>

                    //赞赏信息
                    <span class="token key atrule">msg</span><span class="token punctuation">:</span> <span class="token string">"文章写的非常好"</span><span class="token punctuation">,</span>

                    //用户头像
                    <span class="token key atrule">img</span><span class="token punctuation">:</span> <span class="token string">"https://ooszy.cco.vin/img/blog-public/nc.jpeg"</span><span class="token punctuation">,</span>

                    //打赏金额
                    <span class="token key atrule">price</span><span class="token punctuation">:</span> <span class="token number">11</span><span class="token punctuation">,</span>

                    //金额前缀
                    <span class="token key atrule">prefix</span><span class="token punctuation">:</span> <span class="token string">"￥"</span><span class="token punctuation">,</span>
                <span class="token punctuation">}</span><span class="token punctuation">,</span>
                <span class="token punctuation">{</span>
                    <span class="token key atrule">name</span><span class="token punctuation">:</span> <span class="token string">"初尘"</span><span class="token punctuation">,</span>
