                    <span class="token key atrule">img</span><span class="token punctuation">:</span> <span class="token string">"https://ooszy.cco.vin/img/blog-public/nc.jpeg"</span><span class="token punctuation">,</span>
                    <span class="token key atrule">price</span><span class="token punctuation">:</span> <span class="token number">7</span><span class="token punctuation">,</span>
                    <span class="token key atrule">prefix</span><span class="token punctuation">:</span> <span class="token string">"￥"</span><span class="token punctuation">,</span>
                <span class="token punctuation">}</span><span class="token punctuation">,</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
